<template>
  <div class="container">
    <div class="sidebar">
      <h2><span class="accent">|</span> CRUD OPERATIONS</h2>
      <img
        :src="avatarImage"
        alt="Profile"
        class="avatar"
      />
      <h3>Karthi Madesh</h3>
      <p class="role">Admin</p>
      <ul>
        <li @click="goToDashboard">üè† Home</li>
        <li @click="goToCourses">üìò Course</li>
        <li class="active" @click="goToStudents">üéì Students</li>
        <li @click="goToPayments">üí∞ Payment</li>
        <li @click="goToArchive">üóÇÔ∏è Archive</li>
        <li @click="goToReports">üìä Report</li>
        <li @click="goToSettings">‚öôÔ∏è Settings</li>
      </ul>
      <button class="logout" @click="logout">Logout</button>
    </div>

    <div class="main">
      <div class="header">
        <h2>Students List</h2>
        <button class="add-new-student-button" @click="addStudent">Add New Student</button>
      </div>

      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Email</th>
            <th>Phone</th>
            <th>Enroll Number</th>
            <th>Date of Admission</th>
            <th>Status</th> <!-- –¥–æ–±–∞–≤–ª–µ–Ω–∞ –∫–æ–ª–æ–Ω–∫–∞ –°—Ç–∞—Ç—É—Å -->
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="student in students" :key="student.id">
            <td>
              <img :src="student.avatar || avatarImage" class="avatar-small" />
              <input v-if="student.editing" v-model="student.name"  class="nameSt"/>
              <span v-else>{{ student.name }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.email" />
              <span v-else>{{ student.email }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.phone" />
              <span v-else>{{ student.phone }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.enrollNumber" />
              <span v-else>{{ student.enrollNumber }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.date" type="date" />
              <span v-else>{{ student.date }}</span>
            </td>
            <td>
              <select v-if="student.editing" v-model="student.status">
                <option value="Active">Active</option>
                <option value="Graduate">Graduate</option>
                <option value="Frozen">Frozen</option>
                <option value="Churn">Churn</option>
              </select>
              <span v-else>{{ student.status }}</span>
            </td>
            <td>
              <button v-if="student.editing" @click="saveStudent(student.id)" class="action-button save-button">üíæ</button>
              <button v-else @click="editStudent(student.id)" class="action-button edit-button">‚úèÔ∏è</button>
              <button @click="deleteStudent(student.id)" class="action-button delete-button">üóëÔ∏è</button>
            </td>
          </tr>
          <tr v-if="students.length === 0">
            <td colspan="7" style="text-align: center;">No students found.</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'StudentsView',
  data() {
    return {
      avatarImage: "data:image/jpeg;base64,",
      students: [],
    };
  },
  mounted() {
    this.loadStudentsFromLocalStorage();
  },
  methods: {
    goToDashboard() {
      this.$router.push("/dashboard");
    },
    goToCourses() {
      this.$router.push("/courses");
    },
    goToPayments() {
      this.$router.push("/payments");
    },
    goToStudents() {
      this.$router.push("/students");
    },
    goToReports() {
      this.$router.push("/reports");
    },
    goToSettings() {
      this.$router.push("/settings");
    },
    logout() {
      this.$router.push("/");
    },
    goToArchive() {
      this.$router.push('/archive');
    },

    addStudent() {
      const newId = this.students.length > 0 ? Math.max(...this.students.map(s => s.id)) + 1 : 1;
      const newStudent = {
        id: newId,
        name: "New Student",
        email: "new.student@example.com",
        phone: "111-222-3333",
        enrollNumber: "ENR" + String(newId).padStart(3, '0'),
        date: new Date().toISOString().slice(0, 10),
        avatar: this.avatarImage,
        editing: true,
        status: "Active",
      };
      this.students.push(newStudent);
      this.saveStudentsToLocalStorage();
    },
    editStudent(id) {
      const studentToEdit = this.students.find(student => student.id === id);
      if (studentToEdit) {
        studentToEdit.editing = true;
      }
    },
    saveStudent(id) {
      const studentToSave = this.students.find(student => student.id === id);
      if (studentToSave) {
        studentToSave.editing = false;
        this.saveStudentsToLocalStorage();
      }
    },
    deleteStudent(id) {
      if (confirm("Are you sure you want to delete this student?")) {
        this.students = this.students.filter(student => student.id !== id);
        this.saveStudentsToLocalStorage();
      }
    },
    loadStudentsFromLocalStorage() {
      const storedStudents = localStorage.getItem('students');
      if (storedStudents) {
        this.students = JSON.parse(storedStudents);
      } else {
        this.students = [
          {
            id: 1,
            name: "Emily White",
            email: "emily.white@example.com",
            phone: "555-123-4567",
            enrollNumber: "ENR001",
            date: "2023-09-01",
            avatar: this.avatarImage,
            editing: false,
            status: "Active",
          },
          {
            id: 2,
            name: "David Green",
            email: "david.green@example.com",
            phone: "555-987-6543",
            enrollNumber: "ENR002",
            date: "2023-09-05",
            avatar: this.avatarImage,
            editing: false,
            status: "Active",
          },
          {
            id: 3,
            name: "Sarah Brown",
            email: "sarah.brown@example.com",
            phone: "555-111-2222",
            enrollNumber: "ENR003",
            date: "2023-09-10",
            avatar: this.avatarImage,
            editing: false,
            status: "Active",
          },
        ];
        this.saveStudentsToLocalStorage();
      }
    },
    saveStudentsToLocalStorage() {
      localStorage.setItem('students', JSON.stringify(this.students));
    },
  }
};
</script>

<style scoped>
/* —Å—Ç–∏–ª–∏ –æ—Å—Ç–∞–≤–ª–µ–Ω—ã –±–µ–∑ –∏–∑–º–µ–Ω–µ–Ω–∏–π */
.container {
  display: flex;
}

.sidebar {
  background: #f5f5f5;
  width: 200px;
  padding: 20px;
  text-align: center;
  height: 100vh;
}

.accent {
  color: #facc15;
  margin-right: 5px;
}

.avatar {
  border-radius: 50%;
  width: 100px;
  margin: 20px 0 10px;
}

.avatar-small {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 8px;
  vertical-align: middle;
}

.role {
  color: orange;
  font-size: 14px;
  margin-bottom: 20px;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 10px;
  text-align: left;
  cursor: pointer;
}

li.active,
li:hover {
  background-color: #facc15;
  color: white;
  border-radius: 5px;
}

.logout {
  margin-top: 20px;
  padding: 10px;
  width: 100%;
  background: none;
  border: 1px solid #ccc;
  cursor: pointer;
}

.main {
  flex: 1;
  padding: 20px;
  background: #fff;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.add-new-student-button {
  background-color: #facc15;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.add-new-student-button:hover {
  background-color: #e0b012;
}

table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  box-shadow: 0 0 10px #f5f5f5;
}

th, td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #facc15;
  color: white;
  font-weight: 600;
}

.action-button {
  border: none;
  background: none;
  cursor: pointer;
  font-size: 18px;
  margin-right: 5px;
}

.action-button:hover {
  color: #facc15;
}

.nameSt {
  font-weight: 700;
  font-size: 16px;
}

.main {
  background-color: var(--bg-color);
  color: var(--text-color);
}
</style>
