import { createRouter, createWebHashHistory } from 'vue-router'; // üëà –û–ë–Ø–ó–ê–¢–ï–õ–¨–ù–û hash

import LoginView from '@/components/LoginView.vue';
import DashboardView from '@/components/DashboardView.vue';
import StudentsView from '@/components/StudentsView.vue';
import PaymentsView from '@/components/PaymentsView.vue';
import CourseView from '@/components/CourseView.vue';
import ReportView from '@/components/ReportView.vue';
import SettingsView from '@/components/SettingsView.vue';
import ArchiveView from '@/components/ArchiveView.vue';


const routes = [
  { path: '/', name: 'Login', component: LoginView },
  { path: '/dashboard', name: 'Dashboard', component: DashboardView },
  { path: '/students', name: 'Students', component: StudentsView },
  { path: '/payments', name: 'Payments', component: PaymentsView },
  { path: '/courses', name: 'courses', component: CourseView},
  { path: '/reports', name: 'reports', component: ReportView},
  { path: '/settings', name: 'settings', component: SettingsView},
  { path: '/archive', name: 'Archive', component: ArchiveView,
  },
];

const router = createRouter({
  history: createWebHashHistory(), // üëà –≠–¢–û –û–ë–Ø–ó–ê–¢–ï–õ–¨–ù–û
  routes
});

export default router;
